<script setup>
import { defineOptions, ref, onMounted, onUnmounted} from "vue";
import { debounce } from "lodash";

defineOptions({
  name: "LandingHeader",
});


const landingHeader = ref(null);
const addChatHeaderBGColor = () => {
  if (landingHeader.value) {
    if (window.scrollY > 1) {
      landingHeader.value.classList.add("active");
    } else {
      landingHeader.value.classList.remove("active");
    }
  }
};

const debouncedChangeChatHeaderState = debounce(addChatHeaderBGColor, 100);

onMounted(() => {
  window.addEventListener("scroll", debouncedChangeChatHeaderState);
});

onUnmounted(() => {
  window.removeEventListener("scroll", debouncedChangeChatHeaderState);
});
</script>

